import {Item} from "../models/ItemModel";

import axios from 'axios'
import {useEffect, useState} from "react";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import EditIcon from "@mui/icons-material/Edit";
import {Button} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import {getAllTodosTest, putUpdateItemsNew} from "../service/requestserviceTest";

export default function FullShoppingList(){

    const [items, setItems] = useState<Item[]>([])

    useEffect(() => {
        getAllTodosTest()
            .then(items => setItems(items))
            .catch(error => console.error(error))
    }, [])

    const handleAddOneToQuantityButtonFunc = (item: Item) => {
        const newItems: Item[] = [...items];
        if (newItems.find(i => i.id === item.id )!== undefined){
            // @ts-ignore
            newItems.find(i => i.id === item.id ).quantity++;
        }
        setItems(newItems);
        putUpdateItemsNew(newItems);
    }

    return (
        <div>
            {items.map((item: Item, index) => (
                <div className='item-container' key={index}>
                    <AddIcon onClick={() => {handleAddOneToQuantityButtonFunc(item)}}/>
                    <div className='quantity'>
                        <span className="number-size"> {item.quantity}x </span>
                    </div>
                    {/*<RemoveIcon onClick={() => {handleSubstractButtonFunc(item)}}/>*/}
                    <div className="item">{item.name}</div>
                    {/*bei onClick wird nur die Funktion aufgerufen nichts ausgegebn*/}
                    {/*<EditIcon onClick={() => {handleEditButtonFunc(item)}}/>*/}
                    {/*<Button variant="outlined" onClick={() => {handleRemoveButtonFunc(item)}} startIcon={<DeleteIcon />}>*/}
                    {/*    remove*/}
                    {/*</Button>*/}

                </div>
            ))}
        </div>
    )

}