import axios from "axios";
import {Item} from "../models/ItemModel";

const INDEV = process.env.NODE_ENV === "development"
const BASEURL: string = INDEV ? "http://localhost:8080" : "";

//oder put ? , items oder {items}
export const putUpdateItemsNew: (items: Item[]) => void =
    (items) => {
        axios.put(BASEURL+'/api/shoppinglist/test/Mongo', items).catch(console.error)
    }

export const getAllTodosTest = () =>
    axios.get(BASEURL+'/api/shoppinglist/test/Mongo')
        .then(response => response.data)
        .catch((error) => {
            console.log(error);
        })

